.contactForm{
    width: 50%;
    margin:auto;
    margin-bottom:20px;
    padding-bottom:20px;
    /* background-color:red; */

}
.contactForm>div{
    width:100%;
    display:flex;
   
}
.contactForm>div>label{
    color: #c4c4c4;
    margin-bottom: 5px;
    margin-left: 10px;
  
}

.contactForm>input, .contactForm>textarea{
    display: block;
    width: 100%;
    height: 38px;
    padding: 8px 12px;
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 1.42857143;
    

    background-color: #fff;
    border: 1px solid #ccc;
    background-color: rgba(27,27,27,.91);
    max-width: 650px;
    border-style: none;
    border-width: 1px;
    border-color: hsla(0,0%,100%,0);
    border-radius: 10px;
    color: #fff;

   


    
}

.contactForm>textarea{
    height:200px;
   
}


.form_submit{
    border:none;
    min-height: 48px;
min-width: 118px;
margin-top: 10px;
border-radius: 12px;
color: #c4c4c4;
font-size: 18px;
font-weight: 500;
background-color: rgba(27,27,27,.91);

}


